'use client';

import { useState, useEffect } from 'react';
import { Memory, getMemoriesFromStorage, saveMemoriesToStorage, updateMemory, deleteMemory, memoryCategories } from '@/lib/memoryUtils';
import MemoryCard from '@/components/MemoryCard';
import EditMemoryModal from '@/components/EditMemoryModal';
import LoginScreen from '@/components/LoginScreen';

export default function GiftPage() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [memories, setMemories] = useState<Memory[]>([]);
  const [newMemoryName, setNewMemoryName] = useState('');
  const [newMemoryText, setNewMemoryText] = useState('');
  const [newMemoryCategory, setNewMemoryCategory] = useState<Memory['category']>('romantic');
  const [selectedPhotos, setSelectedPhotos] = useState<File[]>([]);
  const [error, setError] = useState('');
  const [editingMemory, setEditingMemory] = useState<Memory | null>(null);
  const [isEditModalOpen, setIsEditModalOpen] = useState(false);

  useEffect(() => {
    // Check if user is already authenticated
    const authStatus = localStorage.getItem('anniversary-auth');
    if (authStatus === 'true') {
      setIsAuthenticated(true);
    }
  }, []);

  useEffect(() => {
    if (isAuthenticated) {
      try {
        const loadedMemories = getMemoriesFromStorage();
        setMemories(loadedMemories);
      } catch (error) {
        console.error('Error loading memories:', error);
        setError('Error al cargar los recuerdos');
      }
    }
  }, [isAuthenticated]);

  const handleLogin = (username: string, password: string) => {
    if (username === 'mivida' && password === 'convos') {
      setIsAuthenticated(true);
      localStorage.setItem('anniversary-auth', 'true');
      return true;
    }
    return false;
  };

  const handlePhotoChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      setSelectedPhotos(Array.from(e.target.files));
    }
  };

  const convertPhotosToBase64 = async (files: File[]): Promise<string[]> => {
    const promises = files.map(file => {
      return new Promise<string>((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = () => resolve(reader.result as string);
        reader.onerror = reject;
        reader.readAsDataURL(file);
      });
    });
    return Promise.all(promises);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    if (!newMemoryName.trim()) {
      setError('Por favor, escribe un nombre para el recuerdo');
      return;
    }

    if (!newMemoryText.trim()) {
      setError('Por favor, escribe una descripci√≥n del recuerdo');
      return;
    }

    try {
      const photoBase64Array = selectedPhotos.length > 0 
        ? await convertPhotosToBase64(selectedPhotos)
        : [];

      const memory: Memory = {
        id: Date.now().toString(),
        name: newMemoryName.trim(),
        text: newMemoryText.trim(),
        photos: photoBase64Array,
        category: newMemoryCategory,
        coverPhoto: photoBase64Array.length > 0 ? '0' : undefined,
        timestamp: new Date()
      };

      const updatedMemories = [memory, ...memories];
      setMemories(updatedMemories);
      saveMemoriesToStorage(updatedMemories);
      setNewMemoryName('');
      setNewMemoryText('');
      setNewMemoryCategory('romantic');
      setSelectedPhotos([]);
      
      // Reset file input
      const fileInput = document.getElementById('photo-input') as HTMLInputElement;
      if (fileInput) fileInput.value = '';
    } catch (error) {
      console.error('Error saving memory:', error);
      setError('Error al guardar el recuerdo');
    }
  };

  const handleEditMemory = (memory: Memory) => {
    setEditingMemory(memory);
    setIsEditModalOpen(true);
  };

  const handleSaveMemory = (updatedMemory: Memory) => {
    const updatedMemories = updateMemory(updatedMemory);
    setMemories(updatedMemories);
  };

  const handleDeleteMemory = (memoryId: string) => {
    const updatedMemories = deleteMemory(memoryId);
    setMemories(updatedMemories);
  };

  const handleCloseEditModal = () => {
    setIsEditModalOpen(false);
    setEditingMemory(null);
  };

  // Show login screen if not authenticated
  if (!isAuthenticated) {
    return <LoginScreen onLogin={handleLogin} />;
  }

  return (
    <div className="gift-page">
      <header className="gift-header">
        <h1>Regalo del Primer Mes üíï</h1>
        <p>Un lugar especial para guardar nuestros recuerdos m√°s preciados</p>
        <div className="love-message">
          <p>
            Mi vida, nos conocimos mucho en tan poco y aun as√≠ siento que solo con eso me di cuenta que sos lo que quiero para toda mi vida. Cada momento con vos 
            se convierte en un recuerdo que quiero atesorar para siempre.
            Esta p√°gina es para nosotros, para guardar todos esos peque√±os momentos 
            que hacen que nuestro amor sea tan especial.
          </p>
        </div>
      </header>

      <section className="memory-section">
        <h2>Agregar un Nuevo Recuerdo</h2>
        <form onSubmit={handleSubmit} className="memory-form">
          <input
            type="text"
            value={newMemoryName}
            onChange={(e) => setNewMemoryName(e.target.value)}
            placeholder="Nombre del recuerdo (ej: Nuestra primera cita, Primer beso...)"
            className="memory-input"
          />
          
          <div className="category-section">
            <label htmlFor="category-select" className="category-label">
              Tipo de recuerdo
            </label>
            <select
              id="category-select"
              value={newMemoryCategory}
              onChange={(e) => setNewMemoryCategory(e.target.value as Memory['category'])}
              className="category-select"
            >
              {Object.entries(memoryCategories).map(([key, category]) => (
                <option key={key} value={key}>
                  {category.emoji} {category.name}
                </option>
              ))}
            </select>
          </div>

          <textarea
            value={newMemoryText}
            onChange={(e) => setNewMemoryText(e.target.value)}
            placeholder="Escribe aqu√≠ la descripci√≥n de este recuerdo especial..."
            className="memory-textarea"
            rows={4}
          />
          <div className="photo-section">
            <label htmlFor="photo-input" className="photo-label">
              üì∏ Agregar Fotos (opcional)
            </label>
            <input
              id="photo-input"
              type="file"
              multiple
              accept="image/*"
              onChange={handlePhotoChange}
              className="photo-input"
            />
            {selectedPhotos.length > 0 && (
              <div className="selected-photos">
                <p>{selectedPhotos.length} foto(s) seleccionada(s)</p>
                <div className="photo-preview">
                  {selectedPhotos.map((photo, index) => (
                    <span key={index} className="photo-name">
                      {photo.name}
                    </span>
                  ))}
                </div>
              </div>
            )}
          </div>
          {error && <div className="error-message">{error}</div>}
          <button type="submit" className="memory-button">
            Guardar Recuerdo ‚ù§Ô∏è
          </button>
        </form>
      </section>

      <section className="memories-display">
        <h2>Nuestros Recuerdos</h2>
        {memories.length === 0 ? (
          <div className="no-memories">
            <p>A√∫n no hay recuerdos guardados. ¬°Agrega el primero!</p>
          </div>
        ) : (
          <div className="memory-list">
            {memories.map((memory) => (
              <MemoryCard 
                key={memory.id} 
                memory={memory} 
                onEdit={handleEditMemory}
              />
            ))}
          </div>
        )}
      </section>

      {editingMemory && (
        <EditMemoryModal
          memory={editingMemory}
          isOpen={isEditModalOpen}
          onClose={handleCloseEditModal}
          onSave={handleSaveMemory}
          onDelete={handleDeleteMemory}
        />
      )}
    </div>
  );
}
