import { Memory, memoryCategories } from '@/lib/memoryUtils';

interface MemoryCardProps {
  memory: Memory;
  onEdit: (memory: Memory) => void;
}

export default function MemoryCard({ memory, onEdit }: MemoryCardProps) {
  if (!memory) {
    return null;
  }

  const formatDate = (date: Date) => {
    try {
      return new Intl.DateTimeFormat('es-ES', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      }).format(date);
    } catch (error) {
      return 'Fecha no disponible';
    }
  };

  const getCoverPhoto = () => {
    if (memory.coverPhoto && memory.photos.length > 0) {
      const coverIndex = parseInt(memory.coverPhoto);
      return memory.photos[coverIndex] || memory.photos[0];
    }
    return memory.photos[0];
  };

  const category = memory.category ? memoryCategories[memory.category] : memoryCategories.romantic;

  return (
    <div className="memory-card" style={{ borderColor: category.color }}>
      <div className="memory-header">
        <div className="memory-category" style={{ color: category.color, backgroundColor: category.bgColor }}>
          <span className="category-emoji">{category.emoji}</span>
          <span className="category-name">{category.name}</span>
        </div>
        <button onClick={() => onEdit(memory)} className="edit-button" title="Editar recuerdo">
          ‚úèÔ∏è
        </button>
      </div>

      <div className="memory-title-section">
        <h3 className="memory-name">{memory.name}</h3>
        {memory.lastModified && (
          <span className="last-modified">
            Editado: {formatDate(memory.lastModified)}
          </span>
        )}
      </div>
      
      {memory.photos && memory.photos.length > 0 && (
        <div className="memory-photos">
          {memory.photos.length === 1 ? (
            <div className="single-photo">
              <img
                src={getCoverPhoto()}
                alt={`Foto de ${memory.name}`}
                className="memory-photo cover-photo"
                loading="lazy"
              />
            </div>
          ) : (
            <div className="photo-gallery">
              <div className="cover-photo-container">
                <img
                  src={getCoverPhoto()}
                  alt={`Foto principal de ${memory.name}`}
                  className="memory-photo cover-photo"
                  loading="lazy"
                />
                <div className="photo-count">
                  üì∏ {memory.photos.length} fotos
                </div>
              </div>
              <div className="photo-thumbnails">
                {memory.photos.slice(1, 4).map((photo, index) => (
                  <img
                    key={index}
                    src={photo}
                    alt={`Foto ${index + 2} de ${memory.name}`}
                    className="memory-photo thumbnail"
                    loading="lazy"
                  />
                ))}
                {memory.photos.length > 4 && (
                  <div className="more-photos">
                    +{memory.photos.length - 4}
                  </div>
                )}
              </div>
            </div>
          )}
        </div>
      )}
      
      <div className="memory-text">
        {memory.text}
      </div>
      
      <div className="memory-footer">
        <div className="memory-date">
          {formatDate(memory.timestamp)}
        </div>
      </div>
    </div>
  );
}
