'use client';

import { useState } from 'react';

interface LoginScreenProps {
  onLogin: (username: string, password: string) => boolean;
}

export default function LoginScreen({ onLogin }: LoginScreenProps) {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setIsLoading(true);

    // Simulate loading for romantic effect
    await new Promise(resolve => setTimeout(resolve, 1000));

    const loginSuccess = onLogin(username, password);
    
    if (!loginSuccess) {
      setError('Usuario o clave incorrectos. Intenta de nuevo, mi amor üíï');
    }
    
    setIsLoading(false);
  };

  return (
    <div className="login-screen">
      <div className="login-container">
        <div className="login-header">
          <h1>Bienvenida a Nuestro Lugar Especial üíï</h1>
          <p>Ingresa las credenciales que solo t√∫ conoces...</p>
        </div>

        <form onSubmit={handleSubmit} className="login-form">
          <div className="form-group">
            <label htmlFor="username">Usuario</label>
            <input
              id="username"
              type="text"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              placeholder="Escribe el usuario especial..."
              className="login-input"
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="password">Clave</label>
            <input
              id="password"
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="Escribe la clave secreta..."
              className="login-input"
              required
            />
          </div>

          {error && <div className="error-message">{error}</div>}

          <button 
            type="submit" 
            className="login-button"
            disabled={isLoading}
          >
            {isLoading ? 'Verificando... üíñ' : 'Entrar a Nuestros Recuerdos ‚ù§Ô∏è'}
          </button>
        </form>

        <div className="login-hint">
          <p>üí° Pista: Que es lo que Max quiere con vos?</p>
        </div>
      </div>
    </div>
  );
}
