const STORAGE_KEY = 'anniversary-memories';

export interface Memory {
  id: string;
  name: string;
  text: string;
  photos: string[];
  coverPhoto?: string; // Index of the cover photo or URL
  category?: 'romantic' | 'adventure' | 'special' | 'daily' | 'milestone';
  timestamp: Date;
  lastModified?: Date;
}

export function getMemoriesFromStorage(): Memory[] {
  try {
    if (typeof window === 'undefined') {
      return [];
    }
    
    const stored = localStorage.getItem(STORAGE_KEY);
    if (!stored) {
      return [];
    }
    
    const parsed = JSON.parse(stored);
    return parsed.map((memory: any) => ({
      ...memory,
      timestamp: new Date(memory.timestamp),
      lastModified: memory.lastModified ? new Date(memory.lastModified) : undefined
    }));
  } catch (error) {
    console.error('Error loading memories from storage:', error);
    return [];
  }
}

export function saveMemoriesToStorage(memories: Memory[]): void {
  try {
    if (typeof window === 'undefined') {
      return;
    }
    
    localStorage.setItem(STORAGE_KEY, JSON.stringify(memories));
  } catch (error) {
    console.error('Error saving memories to storage:', error);
  }
}

export function updateMemory(updatedMemory: Memory): Memory[] {
  try {
    const memories = getMemoriesFromStorage();
    const index = memories.findIndex(m => m.id === updatedMemory.id);
    
    if (index !== -1) {
      memories[index] = {
        ...updatedMemory,
        lastModified: new Date()
      };
      saveMemoriesToStorage(memories);
    }
    
    return memories;
  } catch (error) {
    console.error('Error updating memory:', error);
    return getMemoriesFromStorage();
  }
}

export function deleteMemory(memoryId: string): Memory[] {
  try {
    const memories = getMemoriesFromStorage();
    const filteredMemories = memories.filter(m => m.id !== memoryId);
    saveMemoriesToStorage(filteredMemories);
    return filteredMemories;
  } catch (error) {
    console.error('Error deleting memory:', error);
    return getMemoriesFromStorage();
  }
}

