'use client';

import { useState, useEffect } from 'react';
import { Memory, memoryCategories } from '@/lib/memoryUtils';

interface EditMemoryModalProps {
  memory: Memory;
  isOpen: boolean;
  onClose: () => void;
  onSave: (updatedMemory: Memory) => void;
  onDelete: (memoryId: string) => void;
}

export default function EditMemoryModal({ 
  memory, 
  isOpen, 
  onClose, 
  onSave, 
  onDelete 
}: EditMemoryModalProps) {
  const [editedMemory, setEditedMemory] = useState<Memory>(memory);
  const [selectedPhotos, setSelectedPhotos] = useState<File[]>([]);
  const [showDeleteConfirm, setShowDeleteConfirm] = useState(false);

  useEffect(() => {
    setEditedMemory(memory);
  }, [memory]);

  const handlePhotoChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      setSelectedPhotos(Array.from(e.target.files));
    }
  };

  const convertPhotosToBase64 = async (files: File[]): Promise<string[]> => {
    const promises = files.map(file => {
      return new Promise<string>((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = () => resolve(reader.result as string);
        reader.onerror = reject;
        reader.readAsDataURL(file);
      });
    });
    return Promise.all(promises);
  };

  const handleAddPhotos = async () => {
    if (selectedPhotos.length > 0) {
      const newPhotosBase64 = await convertPhotosToBase64(selectedPhotos);
      setEditedMemory(prev => ({
        ...prev,
        photos: [...prev.photos, ...newPhotosBase64]
      }));
      setSelectedPhotos([]);
      
      // Reset file input
      const fileInput = document.getElementById('edit-photo-input') as HTMLInputElement;
      if (fileInput) fileInput.value = '';
    }
  };

  const handleRemovePhoto = (index: number) => {
    setEditedMemory(prev => ({
      ...prev,
      photos: prev.photos.filter((_, i) => i !== index)
    }));
  };

  const handleSetCoverPhoto = (index: number) => {
    setEditedMemory(prev => ({
      ...prev,
      coverPhoto: index.toString()
    }));
  };

  const handleSave = () => {
    onSave(editedMemory);
    onClose();
  };

  const handleDelete = () => {
    onDelete(memory.id);
    onClose();
    setShowDeleteConfirm(false);
  };

  if (!isOpen) return null;

  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <div className="modal-header">
          <h2>Editar Recuerdo</h2>
          <button onClick={onClose} className="close-button">√ó</button>
        </div>

        <div className="modal-body">
          <div className="form-group">
            <label htmlFor="edit-name">Nombre del recuerdo</label>
            <input
              id="edit-name"
              type="text"
              value={editedMemory.name}
              onChange={(e) => setEditedMemory(prev => ({ ...prev, name: e.target.value }))}
              className="edit-input"
            />
          </div>

          <div className="form-group">
            <label htmlFor="edit-category">Categor√≠a</label>
            <select
              id="edit-category"
              value={editedMemory.category || 'romantic'}
              onChange={(e) => setEditedMemory(prev => ({ 
                ...prev, 
                category: e.target.value as Memory['category']
              }))}
              className="edit-select"
            >
              {Object.entries(memoryCategories).map(([key, cat]) => (
                <option key={key} value={key}>
                  {cat.emoji} {cat.name}
                </option>
              ))}
            </select>
          </div>

          <div className="form-group">
            <label htmlFor="edit-text">Descripci√≥n</label>
            <textarea
              id="edit-text"
              value={editedMemory.text}
              onChange={(e) => setEditedMemory(prev => ({ ...prev, text: e.target.value }))}
              className="edit-textarea"
              rows={4}
            />
          </div>

          <div className="form-group">
            <label>Fotos actuales</label>
            <div className="current-photos">
              {editedMemory.photos.map((photo, index) => (
                <div key={index} className="photo-item">
                  <img src={photo} alt={`Foto ${index + 1}`} className="edit-photo" />
                  <div className="photo-actions">
                    <button
                      onClick={() => handleSetCoverPhoto(index)}
                      className={`cover-btn ${editedMemory.coverPhoto === index.toString() ? 'active' : ''}`}
                      title="Establecer como portada"
                    >
                      ‚≠ê
                    </button>
                    <button
                      onClick={() => handleRemovePhoto(index)}
                      className="remove-btn"
                      title="Eliminar foto"
                    >
                      üóëÔ∏è
                    </button>
                  </div>
                </div>
              ))}
            </div>
          </div>

          <div className="form-group">
            <label htmlFor="edit-photo-input">Agregar m√°s fotos</label>
            <input
              id="edit-photo-input"
              type="file"
              multiple
              accept="image/*"
              onChange={handlePhotoChange}
              className="photo-input"
            />
            {selectedPhotos.length > 0 && (
              <div className="selected-photos">
                <p>{selectedPhotos.length} foto(s) seleccionada(s)</p>
                <button onClick={handleAddPhotos} className="add-photos-btn">
                  Agregar Fotos
                </button>
              </div>
            )}
          </div>
        </div>

        <div className="modal-footer">
          <button onClick={() => setShowDeleteConfirm(true)} className="delete-btn">
            Eliminar Recuerdo
          </button>
          <div className="action-buttons">
            <button onClick={onClose} className="cancel-btn">
              Cancelar
            </button>
            <button onClick={handleSave} className="save-btn">
              Guardar Cambios
            </button>
          </div>
        </div>

        {showDeleteConfirm && (
          <div className="delete-confirm-overlay">
            <div className="delete-confirm-modal">
              <h3>¬øEst√°s seguro?</h3>
              <p>Esta acci√≥n no se puede deshacer. El recuerdo se eliminar√° permanentemente.</p>
              <div className="confirm-buttons">
                <button onClick={() => setShowDeleteConfirm(false)} className="cancel-btn">
                  Cancelar
                </button>
                <button onClick={handleDelete} className="confirm-delete-btn">
                  S√≠, Eliminar
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
